body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: MoristonPersonal;
  src: url("%PUBLIC_URL%/MoristonPersonal-Regular.otf") format("opentype"); }

@font-face {
  font-family: MoristonPersonal;
  font-weight: bold;
  src: url("%PUBLIC_URL%/MoristonPersonal-Bold.otf") format("opentype"); }

:root {
  --off-white: #232223; }

body {
  background: black; }

.nav {
  font-family: 'Raleway', sans-serif;
  background-color: #000000;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  width: 100%;
  height: 80px;
  z-index: 1000;
  box-shadow: 0 4px 14px 0 rgba(0, 0, 0, 0.15); }

.nav-logo {
  height: 60px;
  width: 60px; }

.nav-logo:hover {
  cursor: pointer; }

.nav .nav-content {
  display: flex;
  align-items: center;
  height: 100%; }

.nav-item {
  display: inline-block;
  justify-content: right;
  margin-left: 2rem;
  color: #dbdbdb;
  -webkit-padding-start: 20px;
          padding-inline-start: 20px; }

.nav-item:hover {
  color: black;
  cursor: pointer; }

.section {
  box-sizing: content-box; }

.section-dark {
  background: #333;
  color: white; }

.section-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 10px 40px 40px 40px; }

* {
  box-sizing: border-box; }

.container {
  width: 100%;
  margin: 0 auto; }

ul li {
  display: inline;
  text-align: center; }

a {
  display: inline-block;
  width: 100%;
  padding: .75rem 0;
  margin: 0;
  text-decoration: none;
  color: #d8d8d8; }

.two:hover ~ hr {
  margin-left: 20%; }

.three:hover ~ hr {
  margin-left: 40%; }

.four:hover ~ hr {
  margin-left: 62%; }

.five:hover ~ hr {
  margin-left: 83%; }

.one {
  -webkit-padding-start: 0px;
          padding-inline-start: 0px; }

hr {
  height: .25rem;
  width: 20%;
  margin: 0;
  background: tomato;
  border: none;
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out; }

h1 {
  font-family: 'MoristonPersonal';
  width: 100%;
  text-shadow: -1px -1px 0 #202a37, 1px -1px 0 #202a37, -1px 1px 0 #202a37, 1px 1px 0 #202a37; }

